.page-index{
  padding-bottom: 25px;
  min-height: calc(100vh);
  background-color: #262626;
  background-image: url(../../assets/rfs/index_bg.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.page-index .app-banner{
  height: 125px;
  background-image: url(../../assets/rfs/banner.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  box-sizing: border-box;
}
.rank-reward-wrap{
  margin: 25px;
  height: 100px;
  padding: 15px;
  background-color: #191919;
  background-image: url(../../assets/rfs/bg.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  box-sizing: border-box;
  border-radius: 5px;
}
.rank-reward-wrap .top{
  margin-bottom: 20px;
  align-items: flex-start;
  color: #fff;
}
.rank-reward-wrap .top .info-row{
  display: flex;
  flex-direction: column;
}
.rank-reward-wrap .top .info-row .num{
  margin-top: 6px;
  font-size: 12px;
}

.pho-detail{
  background-color: #2C2D32;
  border-radius: 10px;
  margin: 25px;
  padding: 25px;
  box-sizing: border-box;
  color: #fff;
}
.pho-detail .top .key{
  font-size: 13px;
  opacity: 0.6;
}
.pho-detail .top .val span{
  font-size: 13px;
  opacity: 0.6;
}
.pho-detail .top .val b{
  font-size: 15px;
  color: #fff;
  opacity: 1;
}
.pho-detail .top .btn-buy{
  width: 87px;
  height: 28px;
  background-image: url(../../assets/rfs/btn_bg2.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  color: #313031;
  line-height: 28px;
}
.pho-detail .title{
  margin-bottom: 10px;
  font-size: 13px;
  color: #fff;
  opacity: 0.6;
  font-weight: bold;
}
.pho-detail .detail-row{
  margin-bottom: 15px;
  justify-content: space-between;
}
.pho-detail .detail-row b{
  font-size: 15px;
}
.pho-detail .detail-row span{
  font-size: 12px;
  opacity: 0.6;
}

.pho-desc{
  border-top: 10px solid #707070;
  padding: 15px 25px;
}
.pho-desc img{
  margin: 0 auto;
  display: block;
  width: 264px;
  height: 264px;
}
.pho-desc > p{
  font-size: 13px;
  font-weight: 400;
  color: #FFFFFF;
}

.pho-copyright{
  border-top: 10px solid #707070;
  padding: 15px 25px;
}
.pho-copyright .title{
  margin-bottom: 22px;
  font-size: 14px;
  font-weight: 400;
  color: #FFFFFF;
  opacity: 0.6;
}
.pho-copyright .imgs{
  flex-wrap: wrap;
}
.pho-copyright .imgs img{
  margin-bottom: 15px;
  display: block;
  width: 130px;
  height: 46px;
}
.pho-footer{
  margin-top: 50px;
}
.pho-footer .flex-row{
  justify-content: space-around;
  padding: 0 50px;
}
.pho-footer .flex-row img{
  display: block;
  width: 40px;
  height: 40px;
}
.pho-footer .right{
  font-size: 13px;
  text-align: center;
  color: #fff;
  opacity: 0.6;
}
